/* General Styles */
html {
    scroll-behavior: smooth;
}

body {
    width: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Product Sans', sans-serif;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
}



/* Sidebar Styles */
.sidebar {
    height: 100vh;
    display: grid;
    place-items: center;
    width: 15%;
    background: transparent;
    position: fixed;
    right: 0;
    z-index: 1;
}

.sidebar ul {
    width: 100%;
    height: 60%;
    background-color: #ffffff;
    list-style-type: none;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: space-evenly;
}

.sidebar li {
    display: flex;

}

.sidebar a {
    color: white;
    text-decoration: none;
    transition: color 0.3s;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: rgb(0, 0, 0);
}

.sidebar li:hover {
 
    background-color: gray;
}


.full-screen-section {
    display: flex;
    width: 100%;
    height: 100vh;
    align-items: center;
    justify-content: space-evenly;
}

.text-container {
    max-width: 50%;
}

.text-container h1 {
    font-size: 3rem;
    margin: 0;
}

.text-container h2 {
    font-size: 1.5rem;
    margin: 10px 0;
}

.text-container p {
    font-size: 1.2rem;
}


.image-container img {
    max-width: 100%;
    height: auto;
}

.section {
    width: 100%;
    height: 100vh;
}

.vertical-sections {
    /* padding: 40px; */
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-between;
}

.section {
    position: relative;
}

.section h2 {
    position: absolute;
    top: 2rem;
    left: 2rem;
    z-index: 1;
}

.vertical-section {
    /* width: 33%; */
    flex: 1;
    /* Each section takes equal width */
    position: relative;
    /* Space between sections */
    overflow: hidden;
    /* Hide overflow */
}

.vertical-section:hover {
    flex: 2;
    transition: ease 1s;

}

.vertical-section:not(:hover) {
    flex: 0.3;
    filter: blur(1px);
    transition: ease 0.5s;
}

.video {
    height: 100vh;
    object-fit: cover;
}

.card {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #ffffff;
    /* Semi-transparent background */
    padding: 10px;
    border-radius: 5px;
}

.card p {
    margin: 0 0 5px 0;
    /* Spacing for description */
}

.card a {
    color: white;
    text-decoration: none;
    margin-right: 10px;
    /* Space between links */
}

.card a:hover {
    color: #ff6347;
    /* Change color on hover */
}